@import url(../fontawesome-free-5.5.0-web/css/all.css);
@import url("https://fonts.googleapis.com/css?family=Ubuntu");
:root {
  --font-size-initial: 12px;
  --font-size-lv2: 14px;
  --font-size-lv3: 16px;
  --font-size-lv4: 18px;
  --spacing-site: 1rem;
  --color-text-main: #000; }

/*
* White space reset */
* {
  padding: 0;
  margin: 0; }

/* Box-sizing fix */
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

.site {
  padding: var(--spacing-site, 2em); }
  .site-content {
    max-width: 60rem;
    margin: 0 auto;
    margin-top: 2rem; }

.wrapper {
  margin: 0 auto;
  padding-left: 2rem;
  padding-right: 2rem; }

.navigation-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: fixed;
  top: 5.5em;
  left: 0;
  margin-right: 5rem;
  width: 100%;
  padding: var(--spacing-site);
  padding-bottom: 5rem; }

.navigation-main ul {
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none; }

.navigation-main li {
  display: block;
  list-style: none outside;
  padding: 1em; }

.navigation-main ul li a {
  display: block;
  padding: 0.5em 1em;
  font-size: 1em;
  background: #FFF;
  text-decoration: none;
  color: #444;
  -webkit-box-shadow: 0 0 1.5rem 0.1rem rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 1.5rem 0.1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem; }

#toggle {
  position: relative;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-line-pack: center;
  align-content: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 1rem 0; }

#toggle .fas {
  display: inline-block;
  margin-right: 0.5rem; }

#toggle .fas.fa-times {
  display: none; }

@media screen and (max-width: 800px) {
  #toggle {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; }
  .navigation-main {
    display: none;
    border-top: 2px solid #000;
    margin-top: 1rem; }
  .navigation-main ul li a {
    text-align: center; }
  .navigation-main ul li:not(:first-child) {
    padding-top: 0.5rem; }
  #main-nav-toggle:checked + header nav.main {
    display: block;
    -webkit-box-flex: 1;
    -ms-flex: 1 0 100%;
    flex: 1 0 100%; }
  #main-nav-toggle:checked + header #toggle .fa-times {
    display: block; }
  #main-nav-toggle:checked + header #toggle .fa-bars {
    display: none; } }

@media screen and (min-width: 800px) {
  .navigation-main ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; }
  .navigation-main ul li:hover {
    display: block; } }

body {
  padding-top: 2rem;
  background-color: #747474; }

header img {
  height: 8rem;
  margin-left: 2rem;
  position: relative;
  bottom: 2.5rem; }

.site-branding {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  margin-right: 5rem;
  width: 100%;
  background-color: #272727;
  padding: var(--spacing-site);
  padding-bottom: 5rem; }

.site-title {
  font-size: 2.8em;
  font-weight: bold;
  color: #FF652F;
  /* text-transform: uppercase; */
  font-family: 'Ubuntu', sans-serif;
  padding-bottom: 0.5rem;
  margin-top: 0.3rem;
  border-bottom: 2px solid #DDD; }

.site-description {
  font-style: italic;
  font-family: 'Ubuntu', sans-serif;
  font-size: 120%;
  color: #14A76C;
  margin-top: 0.5rem; }

.site-footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  margin-right: 5rem;
  width: 100%;
  background-color: #272727;
  padding: var(--spacing-site);
  padding-bottom: 5rem; }

.site-footer .footer-left,
.site-footer .footer-center,
.site-footer .footer-right {
  display: inline-block;
  vertical-align: top; }

/* Footer left */
.site-footer .footer-left {
  width: 40%; }

/* The company logo */
.site-footer h3 {
  color: #FFE400;
  font: normal 36px 'Open Sans', cursive;
  margin: 0; }

.site-footer h3 span {
  color: lightseagreen; }

/* Footer links */
.site-footer .footer-links {
  color: #ffffff;
  margin: 20px 0 12px;
  padding: 0; }

.site-footer .footer-links a {
  display: inline-block;
  line-height: 1.8;
  font-weight: 400;
  text-decoration: none;
  color: inherit; }

.site-footer .footer-company-name {
  color: #fff;
  font-size: 14px;
  font-weight: normal;
  margin: 0; }

/* Footer Center */
.site-footer .footer-center {
  width: 35%; }

.site-footer .footer-center i {
  background-color: #33383b;
  color: #ffffff;
  font-size: 25px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  margin: 10px 15px;
  vertical-align: middle; }

.site-footer .footer-center i.fa-envelope {
  font-size: 17px;
  line-height: 38px; }

.site-footer .footer-center p {
  display: inline-block;
  color: #ffffff;
  font-weight: 400;
  vertical-align: middle;
  margin: 0; }

.site-footer .footer-center p span {
  display: block;
  font-weight: normal;
  font-size: 14px;
  line-height: 2; }

.site-footer .footer-center p a {
  color: lightseagreen;
  text-decoration: none; }

.site-footer .footer-links a:before {
  content: "|";
  font-weight: 300;
  font-size: 20px;
  left: 0;
  color: #fff;
  display: inline-block;
  padding-right: 5px; }

.site-footer .footer-links .link-1:before {
  content: none; }

/* Footer Right */
.site-footer .footer-right {
  width: 20%; }

.site-footer .footer-company-about {
  line-height: 20px;
  color: #fff;
  font-size: 13px;
  font-weight: normal;
  margin: 0; }

.site-footer .footer-company-about span {
  display: block;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 20px; }

.site-footer .footer-icons {
  margin-top: 25px; }

.site-footer .footer-icons a {
  display: inline-block;
  width: 35px;
  height: 35px;
  cursor: pointer;
  background-color: #33383b;
  border-radius: 2px;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;
  margin-right: 3px;
  margin-bottom: 5px; }

/* If you don't want the footer to be responsive, remove these media queries */
@media (max-width: 880px) {
  .site-footer {
    font: bold 14px sans-serif; }
  .site-footer .footer-left,
  .site-footer .footer-center,
  .site-footer .footer-right {
    display: block;
    width: 100%;
    margin-bottom: 40px;
    text-align: center; }
  .site-footer .footer-center i {
    margin-left: 0; } }
